import {
  trigger,
  style,
  animate,
  transition,
  query,
  group,
  animateChild
} from '@angular/animations';

export const fadeInAnimation =
  trigger('routeAnimations', [
    transition('* <=> *', [
      style({ position: 'relative' }),
      query(':enter, :leave', [
        style({
          position: 'absolute',
          top: 0,
          left: 0,
          width: '100%',
          opacity: 1
        })
      ], 
      { optional: true }),
      query(':enter', [
        style({ opacity: 0})
      ], 
      { optional: true }),
      query(':leave', animateChild(), 
      { optional: true }),
      group([
        query(':leave', [
          animate('300ms ease-out', style({ opacity: 0}))
        ], 
        { optional: true }),
        query(':enter', [
          animate('300ms ease-out', style({ opacity: 1}))
        ], 
        { optional: true })
      ]),
      query(':enter', animateChild(), 
      { optional: true }),
    ])
  ]);